export default {
	data() {
		return {
			yzmstate: 0,
			yzmhtml:'获取验证码',
			activeColor: this.beforeColor
			//#2CC17B greyColor:'#c5cddb'
		}
	},
	onLoad() {
		
	},
	created() {
		
	},
	props:{
		time:Number,
		beforeColor:String,
		afterColor:String,
		temp:Number
	},
	methods: {
		sendMsg() {
			let t = this.time;
			if (this.yzmstate == 0) {
				this.yzmstate = 1;
				this.activeColor = this.afterColor;
				this.yzmhtml = t + 'S后重发';
				this.sendDo();//发送
				let inter = setInterval(() => {
					t--;
					this.yzmhtml = t + 'S后重发';
					if (t == 0) {
						clearInterval(inter);
						this.yzmstate = 0;
						this.yzmhtml = '获取验证码';
						this.activeColor = this.beforeColor;
					}
				}, 1000);
			}
			
		},
		sendDo() {
			uni.request({
				url: this.$serverUrl + '/api/login/sendMsg',
				method: 'POST',
				data: {
					phone_num: this.$parent.username
				},
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('请求失败', ret);
						return;
					}
					if (ret.data.code == 0) {
						// 气泡提示
						uni.showToast({
							title: ret.data.message,
							icon:'none',
							duration: 2000
						});
						
					} else {
						// 关闭动画
						uni.hideLoading();
						// 气泡提示
						uni.showToast({
							title: ret.data.message,
							icon: 'none',
							duration: 2000
						});
					}
				}
			});
		}
	}
}
